.promo {
  position: relative;
  background-color: $color-layout;
}

.promo::after {
  position: absolute;
  content: " ";
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: $color-layout;
}

.promo__wrap {
  max-width: 1366px;
  margin: 0 auto;
}

.promo img {
  width: 100%;
  height: 463px;
}

.promo__text {
  display: block;
  position: absolute;
  z-index: 1;
  left: 0;
  right: 0;
  top: 39%;

  text-align: center;
  color: $color-default-white;
  font-family: $ff-basic;

  @media (max-width: $tablet-width) {
    margin-right: 15px;
  }

  @media (max-width: $mobile-width) {
    margin-right: 0;
    top: 41%;
  }
}

.promo h1 {
  font-size: 45px;
  font-weight: 700;
  line-height: 58px;
  letter-spacing: 0.03em;
  margin: 0;
  margin-bottom: 5px;

  @media (max-width: $mobile-width) {
    font-size: 38px;
    line-height: 49px;
    margin: 0;
  }
}

.promo__text p {
  font-size: 38px;
  font-style: italic;
  font-weight: 400;
  margin: 0;

  @media (max-width: $mobile-width) {
    font-size: 34px;
    line-height: 44px;
    margin-top: -6px;
  }
}
